import { Term } from './term'

export class Fuzzy extends Term {

    constructor(field: string, value: any) {
        super(field, value);
        this.name = 'fuzzy';
    }

    public static make(field: string, value: any): Fuzzy {
        return new Fuzzy(field, value);
    }

    public fuzziness(value: any = 'AUTO'): this {
        return this.set('fuzziness', value);
    }

    public prefixLength(value: number): this {
        return this.set('prefix_length', value);
    }

    public maxExpansions(value: number): this {
        return this.set('max_expansions', value);
    }

    public transpositions(value: boolean): this {
        return this.set('transpositions', value);
    }
}